import React from 'react';
import Immutable from 'immutable';
import { Q, Grade, DEFAULT_PROVINCE_LIST } from 'js/quantum';
import { setCurrentNav } from 'js/paper/common';
import { Row, Col } from 'react-flexbox-grid'
import Select, {Option, OptGroup} from 'rc-select';
export class OmegaPaperList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            papers: [],
        };
        this.terms = [];
        this.sources = [];
        this.cities = [];
        this.termMap = {};
        this.sourceMap = {};
    }

    componentDidMount() {
        setCurrentNav('试卷列表')
        //这是获取学期
        let term = Q.get('/general/getDict', {query: {type: 1}})
        //这是来源
        let source = Q.get('/general/getDict', {query: {type: 2}})
        //这是试卷类型
        let cities = Q.get('/general/getCity')
        $.when(term, source, cities)
            .done((terms, sources, cities) => {
                this.terms = terms;
                this.sources = sources;
                this.cities = cities;
                this.terms.unshift({
                    code: 0,
                    name: '所有'
                })
                this.sources.unshift({
                    code: 0,
                    name: '所有'
                })
                this.terms.forEach(term => {
                    this.termMap[term.code] = term
                })

                this.sources.forEach(source => {
                    this.sourceMap[source.code] = source
                })
                this.onFilterChange({
                    year: new Date().getFullYear(),
                    term: 0,
                    source: 0,
                    province: '',
                    city: ''
                })
            })
    }

    onFilterChange = (query) =>{
        Q.get('/api/omega_papers',
               { query: Object.assign({}, query, {city : encodeURIComponent(query.city), province : encodeURIComponent(query.province)}) })
        .done((json) => { this.setState({ papers: Immutable.fromJS(json)}); },()=>{console.log(this.state.papers)});
    }
    createPaper(event) {
        event.preventDefault();
        Q.put('/api/omega_papers', { json: {
                name: '',
                year: new Date().getFullYear(),
                edu: Global.user.edu,
                grade: Grade.kSenior_3,
                cat: '',
                doc: '',
                deleted: false,
                districts: [],
                term: parseInt(Global.user.edu*10+1),
                source: 2,
                province: [],
                city: [],
                categories: [],
                pdf: '',
                item_ids: [],
            } })
            .done((paper) => {
                location.href = `/omega_paper/${paper._id}`;
            })
        ;
    }
    render() {
        return (
            <div>
                <Row style={{'marginBottom': '8px'}}>
                    <Col xsOffset={11} xs={1}>
                        <button className='saveBtn' onClick={this.createPaper}>创建试卷</button>
                    </Col>
                </Row>
                <PaperListFilter onFilterChange={this.onFilterChange} terms={this.terms} sources={this.sources} cities={this.cities}/>
                <table className="table table-striped table-hover">
                    <thead>
                        <tr>
                            <th width="5%"> 序号 </th>
                            <th width="40%"> 名称 </th>
                            <th width="10%"> 年份 </th>
                            <th width="15%"> 学期 </th>
                            <th width="20%"> 来源 </th>
                            <th width="10%"> ID </th>
                        </tr>
                    </thead>
                    <tbody>
                        { this.state.papers.map((paper, index) =>
                            <PaperRow paper={paper} index={index} key={index} terms={this.termMap} sources={this.sourceMap} />) }
                    </tbody>
                </table>
                <hr />
            </div>
        );
    }
}

class PaperListFilter extends React.Component {
    constructor(props) {
        super(props);
        this.provinces = {};
        this.years = []
        for(let i= new Date().getFullYear(); i > 2000; i--) {
            this.years.push({
                name:i,
                val:i
            })
        }
        this.years.unshift({ name: '所有年份', val: 0})
    }

    state = {
        year: new Date().getFullYear(),
        term: 0,
        source: 0,
        province: '',
        city: ''
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.terms.length && nextProps.sources.length && nextProps.cities.length) {
            nextProps.cities.forEach(val => {
                this.provinces[val.province_name] = val.city
            })
        }
    }
    changeYear = (value) => {
        this.setState({
            year: value
        }, () => {
            this.props.onFilterChange(this.state)
        })
    }
    changeTerm = (value) => {
        this.setState({
            term: value
        }, () => {
            this.props.onFilterChange(this.state)
        })
    }
    changeSource = (value) => {
        this.setState({
            source: value
        }, () => {
            this.props.onFilterChange(this.state)
        })
    }
    changeProvince = (value) => {
        this.setState({
            province: value,
            city: ''
        }, () => {
            this.props.onFilterChange(this.state)
        })
    }
    changeCity = value => {
        this.setState({
            city: value
        }, () => {
            this.props.onFilterChange(this.state)
        })
    }
    render() {
        let { year, term, source, province, city } = this.state;
        let { terms, sources } = this.props;
        let provinceOpt = [];
        for (let key in this.provinces) {
            provinceOpt.push(<Option key={key} value={key}>{key}</Option>)
        }
        provinceOpt.unshift(<Option key={'provinceAll'} value={""}>所有</Option>)
        let cities =  province ? this.provinces[province]: []
        let cityList = cities.map((item, index) => (<Option key={`city${index}`} value={item.city_name}>{item.city_name}</Option>))
        cityList.unshift(<Option key={'cityAll'} value={""}>所有</Option>)
        return (
            <Row>
                <Col xs>
                    <label style={{'marginRight': '10px'}}>年份</label>
                    <Select style={{ width: "70%" }} onChange={this.changeYear} showSearch={false} optionLabelProp="children" value={year}>
                        {
                            this.years.map((item, index) => (<Option key={`year${index}`} value={item.val}>{item.name}</Option>))
                        }
                    </Select>
                </Col>
                <Col xs>
                    <label style={{'marginRight': '10px'}}>学期</label>
                    <Select style={{ width: "70%" }} onChange={this.changeTerm} showSearch={false} optionLabelProp="children" value={term}>
                        {
                            terms.map((item, index) => (<Option key={`term${index}`} value={item.code}>{item.name}</Option>))
                        }
                    </Select>
                </Col>
                <Col xs>
                    <label style={{'marginRight': '10px'}}>来源</label>
                    <Select style={{ width: "70%" }} onChange={this.changeSource} showSearch={false} optionLabelProp="children" value={source}>
                        {
                            sources.map((item, index) => (<Option key={`source${index}`} value={item.code}>{item.name}</Option>))
                        }
                    </Select>
                </Col>
                <Col xs>
                    <label style={{'marginRight': '10px'}}>省份</label>
                    <Select style={{ width: "70%" }} onChange={this.changeProvince} showSearch={false} optionLabelProp="children" value={province}>
                        {
                            provinceOpt
                        }
                    </Select>
                </Col>
                <Col xs>
                    <label style={{'marginRight': '10px'}}>城市</label>
                    <Select style={{ width: "70%" }} onChange={this.changeCity} showSearch={false} optionLabelProp="children" value={city}>
                        {
                            cityList
                        }
                    </Select>
                </Col>
            </Row>
        );
    }
}

export default OmegaPaperList;


function PaperRow(props) {
    return (
        <tr data-id={props.paper.get('_id')} >
            <td> {props.index + 1} </td>
            <td>
                <a href={`/omega_paper/${props.paper.get('_id')}`}>{props.paper.get('name')}</a>
            </td>
            <td> {props.paper.get('year')} </td>
            <td> {props.paper.get('term') == 0 ? '无': props.terms[props.paper.get('term')].name} </td>
            <td> {props.paper.get('source') == 0 ? '无': props.sources[props.paper.get('source')].name } </td>
            <td className="code"> {props.paper.get('_id')} </td>
        </tr>
    );
}



